<template>
  <div>
    <textarea name="content" id="editor" disabled class="w-100"> </textarea>
  </div>
</template>
<script src="https://nekoinvest.io/js/ckeditor.js"></script>
<script>
let editor
export default {
  name: 'CkEditor',
  props: ['value'],
  data() {
    return {
      editor: null,
    }
  },
  computed: {
    // Handle
    isSync: function () {
      return this.editor ? this.value === this.editor?.getData() : true
    },
  },
  watch: {
    isSync: function (val) {
      if (!val) {
        this.editor?.setData(this.value)
      }
    },
  },
  created() {
    setTimeout(
      function () {
        ClassicEditor.create(document.querySelector('#editor'), {
          toolbar: [
            'heading',
            '|',
            'alignment',
            '|',
            'bold',
            'italic',
            'underline',
            'strikethrough',
            'link',
            'removeFormat',
            '|',
            'undo',
            'redo',
            'findAndReplace',
            '|',
            'fontsize',
            'fontColor',
            'fontBackgroundColor',
            'blockQuote',
            'code',
            '|',
            'outdent',
            'indent',
            '|',
            'bulletedList',
            'numberedList',
            '|',
            'uploadImage',
            'insertTable',
            'mediaEmbed',
            '|',

            // 'fontfamily',
          ],
          image: {
            // You need to configure the image toolbar, too, so it uses the new style buttons.
            toolbar: [
              'imageTextAlternative',
              '|',
              'imageStyle:block',
              'imageStyle:alignLeft',
              'imageStyle:alignRight',
              'imageStyle:inline',
              'imageStyle:side',
              'imageStyle:alignCenter',
              'imageStyle:alignBlockLeft',
              'imageStyle:alignBlockRight',
              'resizeImage:50',
              'resizeImage:75',
              'resizeImage:original',
            ],
          },
          extraPlugins: [MyCustomUploadAdapterPlugin],
        })
          .then((e) => {
            editor = e
            this.editor = e
            this.editor.setData(this.value)
            this.editor.model.document.on('change', (evt) => {
              this.$emit('input', this.editor.getData())
            })
          })
          .catch((error) => {
            console.error(error)
          })
      }.bind(this),
      1000
    )
  },
}
class MyUploadAdapter {
  constructor(loader) {
    // The file loader instance to use during the upload.
    this.loader = loader
  }

  // Starts the upload process.
  upload() {
    return this.loader.file.then(
      (file) =>
        new Promise((resolve, reject) => {
          this._initRequest()
          this._initListeners(resolve, reject, file)
          this._sendRequest(file)
        })
    )
  }

  // Aborts the upload process.
  abort() {
    if (this.xhr) {
      this.xhr.abort()
    }
  }

  // Initializes the XMLHttpRequest object using the URL passed to the constructor.
  _initRequest() {
    const xhr = (this.xhr = new XMLHttpRequest())

    // Note that your request may look different. It is up to you and your editor
    // integration to choose the right communication channel. This example uses
    // a POST request with JSON as a data structure but your configuration
    // could be different.
    xhr.open('POST', 'https://colorme.vn/api/v3/upload-image-public', true)
    xhr.responseType = 'json'
  }

  // Initializes XMLHttpRequest listeners.
  _initListeners(resolve, reject, file) {
    const xhr = this.xhr
    const loader = this.loader
    const genericErrorText = `Couldn't upload file: ${file.name}.`

    xhr.addEventListener('error', () => reject(genericErrorText))
    xhr.addEventListener('abort', () => reject())
    xhr.addEventListener('load', () => {
      const response = xhr.response

      // This example assumes the XHR server's "response" object will come with
      // an "error" which has its own "message" that can be passed to reject()
      // in the upload promise.
      //
      // Your integration may handle upload errors in a different way so make sure
      // it is done properly. The reject() function must be called when the upload fails.
      if (!response || response.error) {
        return reject(
          response && response.error ? response.error.message : genericErrorText
        )
      }

      // If the upload is successful, resolve the upload promise with an object containing
      // at least the "default" URL, pointing to the image on the server.
      // This URL will be used to display the image in the content. Learn more in the
      // UploadAdapter#upload documentation.
      console.log('response resolve', response)
      resolve({
        default: response.link,
      })
    })

    // Upload progress when it is supported. The file loader has the #uploadTotal and #uploaded
    // properties which are used e.g. to display the upload progress bar in the editor
    // user interface.
    if (xhr.upload) {
      xhr.upload.addEventListener('progress', (evt) => {
        if (evt.lengthComputable) {
          loader.uploadTotal = evt.total
          loader.uploaded = evt.loaded
        }
      })
    }
  }

  // Prepares the data and sends the request.
  _sendRequest(file) {
    // Prepare the form data.
    const data = new FormData()

    data.append('upload', file)

    // Important note: This is the right place to implement security mechanisms
    // like authentication and CSRF protection. For instance, you can use
    // XMLHttpRequest.setRequestHeader() to set the request headers containing
    // the CSRF token generated earlier by your application.

    // Send the request.
    this.xhr.send(data)
  }
}

// ...

function MyCustomUploadAdapterPlugin(editor) {
  editor.plugins.get('FileRepository').createUploadAdapter = (loader) => {
    // Configure the URL to the upload script in your back-end here!
    return new MyUploadAdapter(loader)
  }
}
</script>

<style>
#editor,
.ck-blurred.ck.ck-content.ck-editor__editable.ck-rounded-corners.ck-editor__editable_inline,
.ck.ck-content.ck-editor__editable.ck-rounded-corners.ck-editor__editable_inline.ck-focused {
  min-height: 50vh;
}
</style>
